import{showMessage} from '@/api/grabage/GrabList.js'
export default {
	data() {
		return {
			messages:[],
			setTime:[],
		}
	},
	methods: {
		getMessages(){
			showMessage({guid:this.$store.state.userId}).then(res=>{
				if(res.data.code==200){
					console.log(res.data.data);
					this.messages=res.data.data;
				}
			});
		},
		showDate(data){
			console.log(data);
			let arr=data.split(" ");
			console.log(arr);
			return arr[0];
		},
		showTime(data){
			console.log(data);
			let arr=data.split(" ");
			console.log(arr);
			return arr[1];
		},
		check(data,index){
			console.log(index);
			if(index>0){
				let newtime=data.addTime.split(" ");
				let oldtime=this.messages[index-1].addTime.split(" ");
				if(newtime[0]==oldtime[0]){
					return false;
				}else{
					return true;
				}
			}else{
				return true;
			}
			
		}
		
	},
	onLoad() {
		this.getMessages();
	}
}
